import React, { useEffect } from 'react';
import { View, Text, StyleSheet, Image, TouchableOpacity, FlatList, ScrollView } from 'react-native';
import data from '../Database/data';
import database from '../Database/Config';
import { getDatabase, ref, onValue, set } from "firebase/database";

export default function Parking({ navigation }) {
    const [data, setData] = React.useState([]);

    useEffect(() => {

        const starCountRef = ref(database, "/Parking/bookingsPanel/");
        onValue(starCountRef, (snapshot) => {
            const data = snapshot.val();
            if (data) {
                setData(Object.values(data));
                // console.log(Object.values(data).length);
            }
        });

    }, []);

    const ListItem = ({ data }) => {

        // console.log("this is a data")
        // console.log(data.item.username)
        return (
            <View style={{ marginTop: 15, }}>
                <View style={{ flexDirection: 'row', width: 900, alignItems: "center", justifyContent: "center", marginBottom: 5 }}>


                    <View style={{ width: 150, paddingLeft: 20 }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.username}
                        </Text>
                    </View>

                    <View style={{ width: 150, alignItems: "center", }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.vehicleNo}
                        </Text>
                    </View>

                    <View style={{ width: 150, alignItems: "center", }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.phoneNo}
                        </Text>
                    </View>

                    <View style={{ width: 150, alignItems: "center", }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.duration} hrs
                        </Text>
                    </View>

                    <View style={{ width: 150, }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.parkingName}
                        </Text>
                    </View>

                    <View style={{ width: 150, alignItems: "center", }}>
                        <Text style={{ fontWeight: "500", color: "#6E7079" }}>
                            {data.item.amount}
                        </Text>
                    </View>


                </View>
                <View style={{ height: 2, width: 900, backgroundColor: "#fba6ac", marginLeft: 10 }} />
            </View>
        )

    }
    return (
        <View style={styles.container}>
            <View style={{ flexDirection: 'row', width: 920, justifyContent: "space-between" }}>
                <TouchableOpacity onPress={() => { writeData() }} style={styles.stats}>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        Today's Earning
                    </Text>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        â‚¹0
                    </Text>
                </TouchableOpacity>

                <View style={styles.stats}>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        Today's Bookings
                    </Text>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        0
                    </Text>
                </View>

                <View style={styles.stats}>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        Active Bookings
                    </Text>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        0
                    </Text>
                </View>

                <View style={styles.stats}>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        Vacant Spots
                    </Text>
                    <Text style={{ color: '#fff', fontWeight: '500', fontSize: 20 }}>
                        0
                    </Text>
                </View>
            </View>

            <View style={{ width: 920, marginTop: 20 }}>
                <Text style={{ fontSize: 18, fontWeight: "500" }}>Bookings</Text>

                <View style={{ marginTop: 10, backgroundColor: "white", height: 450, borderRadius: 15, }}>

                    <View style={{ flexDirection: 'row', backgroundColor: "#fba6ac", paddingVertical: 10, borderTopRightRadius: 15, borderTopLeftRadius: 15 }}>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Name
                            </Text>
                        </View>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Vehicle Number
                            </Text>
                        </View>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Phone Number
                            </Text>
                        </View>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Duration
                            </Text>
                        </View>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Parking Name
                            </Text>
                        </View>

                        <View style={{ width: 150, alignItems: "center", }}>
                            <Text style={{ fontWeight: "500", fontSize: 16 }}>
                                Amount
                            </Text>
                        </View>

                    </View>

                    <FlatList
                        data={data}
                        keyExtractor={(item, index) => index.toString()}
                        renderItem={(item) => <ListItem data={item} />}
                    // style={{ backgroundColor: '#FDFAE7', paddingTop: 10 }}
                    />


                </View>
            </View>

        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: '#fdced2',
        paddingTop: 20,
        alignItems: "center"
    },
    stats: {
        backgroundColor: '#B01432',
        padding: 10,
        width: 200,
        borderRadius: 10,
        // marginHorizontal: 15,
        paddingVertical: 15,

        shadowColor: '#000',
        shadowOffset: {
            width: 0,
            height: 1,
        },
        shadowOpacity: 0.15,
        shadowRadius: 4,
        elevation: 5,
    },
});
